import './ProductInfo.scss';
import ProductsInfo from '../../assets/Products/ProductInfo';
import ProductInfoBlock from '../../conponents/ProductInfoBlock/ProductInfoBlock';

const ProductInfo = () => {
    return(
        <div className="productInfo">
            <div className="productInfo-top">
                {
                    ProductsInfo.map((item, inex) => (
                        <ProductInfoBlock
                            title={item.title}
                            ime={item.ime}
                        />
                    ))
                }
            </div>

            <div className="productInfo-bottom">
                <h4>Рядом есть</h4>

                <a href="">
                    Больница, поликлиника, Детская, площадка, Детский сад, Остановки, Парк, зелёная зона, Развлекательные заведения, Рестораны, кафе
                </a>
            </div>
        </div>
    )
}

export default ProductInfo;