import { Swiper, SwiperSlide } from 'swiper/react';
import {ArrowBold} from '../../assets/Icons/Icons';
import './HomeSliderBlock.scss';

const HomeSliderBlock = ({title, img, color1, color2}) => {
    return(
            <div className="block" style={{background: `linear-gradient(315deg, ${color1} 0 45%, ${color2} 45% 100%)`}}>
                <div className="info">
                    <h3>
                        {title}
                    </h3>

                    <h5>
                        987 обьявлениий  <span><ArrowBold/></span>
                    </h5>   

                </div>

                <div className="img">
                    <img src={img} alt=""/>
                </div>

            </div>
        
    )
}

export default HomeSliderBlock;