import './assets/styles/main.scss';
import Header from './containers/Header/Header';
import Home from './pages/Home'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from 'react-router-dom'
import {useState} from 'react'
import Footer from './containers/Footer/Footer';
import Chekin from './pages/Checkin';
import SignUp from './pages/SignUp';
import Login from './innerPages/LoginUp';
import Business from './pages/Business';
import Add from './pages/Add';
import AddFinish from './pages/AddFinish';
import Filter from './pages/Filter';

const App = () => {
  const  [token, setToken] = useState(window.localStorage.getItem('sessionToken'))
  
  const handleExit = () => {
    setToken('false')
    window.localStorage.removeItem('sessionToken')
  }
  return (
    <div>
      <Router>
        <div className="header">
          <Header token={token}handleExit={handleExit}/>
        </div>

        <div className="body">
              {
                token ? (
                  <Switch>
                  <Route path="/Home" render={() => <Home setToken={setToken} token={token}/>} />
                  <Route exact path="/">
                    <Redirect to="/Home" />
                  </Route>
                  <Route path="/product" component={Chekin} />
                  <Route path="/add" component={Add} />
                  <Route path="/addFinish" component={AddFinish}/>
                  <Route path="/signup" component={SignUp}/>
                  <Route path="/filter" component={Filter}/>
                  <Route path="/signup/login" render={() => <Login setToken={setToken} token={token}/>} />
              </Switch>
                ) : (
                  <Switch>
                    <Route path="/Home" render={() => <Home setToken={setToken} token={token}/>} />
                    <Route exact path="/">
                      <Redirect to="/Home" />
                    </Route>
                    <Route path="/product" component={Chekin} />
                    <Route path="/signup" component={SignUp}/>
                    <Route path="/filter" component={Filter}/>
                    <Route path="/signup/login" render={() => <Login setToken={setToken} token={token}/>} />
              </Switch>
                )
              }
        </div>


        <div className="underFooter">
            <Footer/>
        </div>
      </Router>
    </div>
  );
}

export default App;
