import './main.scss';
import BtnWrapper from '../containers/BtnSlider/BtnSlider';
import { useState } from 'react';
import BtnBlockAdd from '../containers/BtnBlockAdd/BtnBlockAdd';


const Add = () => {

    const [classlist, setClasslist] = useState(false)
    return(
        <div className="wrpAdd">
            <div className="add">
                <div className="add-step1 container">
                    
                    <div className="add-step1-title">
                        <h2>Добавить обьявления бесплатно</h2>
                        <span>Для добавления вашего обьявлении вы должны выбрать категорию</span>
                    </div>

                    <div className="btnWrp">
                        <BtnWrapper classlist={classlist} setClasslist={setClasslist} />
                    </div>
                </div>

                <div className={`add-step2 ${classlist ? ("active") : ("")}`}>
                    <BtnBlockAdd setClasslist={setClasslist} classlist={classlist}/>
                </div>
            </div>
        </div>
    )
}

export default Add;