import './Footer.scss';
import { FooterLogo, AppleStore, GooglePlay } from '../../assets/Icons/Icons';
import './Footer.scss';
import HomeFooterUnder from '../HomeFooterUnder/HomeFooterUnder';
import FooterContainer from '../FooterContainer/FooterContainer';


const Footer = () => {
    return(
        <div className="footer">
            <div className="footer-top container">
                <div className="footer-top-items">
                    <a href="" className="footer-top-items__link">
                        <FooterLogo />
                    </a>
                    <p>
                        Продай, найди, купи <br />
                    все что ты пожелаешь
                </p>
                </div>

                <div className="footer-top-items">
                    <p>
                        Веб сайт бесплатных обьявлений | на базе <br />
                    имеется 234 944 999 шт обяления
                </p>
                </div>

                <div className="footer-items">
                    <a href="" className="footer-top-items__btns">
                        <AppleStore />
                    </a>
                    <a href="" className="footer-top-items__btns">
                        <GooglePlay />
                    </a>
                </div>
            </div>

            <div className="footer-bottom">
                <HomeFooterUnder/>
                <FooterContainer/>
            </div>
        </div>
    )
}

export default Footer;