import { Link, Redirect, Switch } from 'react-router-dom';
import './HeaderFilter.scss';


const HeaderFilter = ({token}) => {
    return(
        <div className="header-filter">
            <div className="header-filter-left">
                <select name="" id="">
                    <option value="">Любая категория</option>
                </select>
                <input type="search" placeholder="Что будем искать ?"/>
                <select name="" id="">
                    <option value="">По всей Узбекистану</option>
                </select>
                <button>Найти</button>
            </div>

            <div className="header-filter-right">
                <button onClick={
                    () => {
                        token ? (
                            window.location.assign('/add')

                        ) : (
                            window.location.assign('/signup/login')
                        )
                    }                    
                }>Добавить обьявления</button>
            </div>
        </div>
    )
}

export default HeaderFilter;