import { Link } from 'react-router-dom';
import './main.scss';
import { ArrowBold } from '../assets/Icons/Icons';

const SignUp3 = () => {
    return (
        <div className="signup3">
            <div className="signup3-top">
                <h3>
                    Бесплатная регистрация
                </h3>
            </div>

            <div className="signup3-row">
                <p>
                    Код отправлен в виде смс на указанный номер телефонаенциальности
                 </p>
            </div>

            <div className="signup3-center">
                <div className="row">
                    <label htmlFor="">Введите ваш email</label>
                    <div className="row-inner">
                        <input type="text" value="998 97 772 13 44" />
                        <div className="sekond">
                            <h3>90 <span>s</span></h3>
                        </div>
                    </div>
                </div>
                <div className="row1">
                    <input type="text" placeholder="Введите код подтверждения" />
                </div>
            </div>

            <div className="signup3-btnWrapper">
                <Link to="/signup/login" className="next">Получить доступ</Link>
            </div>

            <div className="signup3-bottom">
                <span>
                    или
                </span>


                <a href="">
                    Войдите в систему, если у вас уже есть <br />
                     аккаунт kivi.uz

                </a>
            </div>
        </div>
    )
}

export default SignUp3;