import { Link } from 'react-router-dom';
import './main.scss';
import {ArrowBold} from '../assets/Icons/Icons';

const SignUp2 = () => {
    return (
        <div className="signup2">
            <div className="signup2-top">
                <h3>
                    Бесплатная регистрация
                </h3>
            </div>

            <div className="signup2-center">
                <div className="row">
                    <label htmlFor="">Введите ваш email</label>
                    <input type="text" placeholder="example@kivi.uz" />
                </div>
                <div className="row">
                    <label htmlFor="">Введите ваш email</label>
                    <div className="row-inner">
                        <input type="text" placeholder="998 " />
                        <input type="text" placeholder="97 772 13 44" />
                    </div>
                </div>
            </div>

            <div className="signup2-btnWrapper">
                <div className="row">
                    <p>
                        <a href="">Нажимая на кнопку «Получить доступ», я даю согласие на обработку персональных данных и соглашаюсь c условиями</a> договора-оферты и политикой конфиденциальности
                    </p>
                </div>


                <div className="row1">
                    <Link to="/signup" className="prev"><ArrowBold/></Link>
                    <Link to="/signup/step3" className="next">Получить доступ</Link>
                </div>
            </div>

            <div className="signup1-bottom">
                <span>
                    или
                </span>


                <a href="">
                    Войдите в систему, если у вас уже есть <br />
                     аккаунт kivi.uz

                </a>
            </div>
        </div>
    )
}

export default SignUp2;