import BtnSlider from '../containers/BtnSlider/BtnSlider';
import {
    Route,
    Switch,
    NavLink
} from 'react-router-dom';
import './main.scss';
import {
    HomeNew,
    HomeBest
} from '../innerPages/';
import {ArrowBold} from '../assets/Icons/Icons';
import HomeSlider from '../containers/HomeSlider/HomeSlider';
import HomeFooterSlider from '../containers/HomeFooterSlider/HomeFooterSlider';
import BtnBlock from '../containers/BtnBlock/BtnBlock';
import { useState } from 'react';




const Home = () => {
    const [classlist, setClasslist] = useState(false)
    return(
        <div className="home">
            <div className="home-slider active">
                <div className={`activeHome ${classlist ? ("active") : ("")}`}>

                </div>
                <div className="sliderBtn">
                    <BtnSlider classlist={classlist} setClasslist={setClasslist}/>
                    <BtnBlock />
                </div>
            </div>

            <div className="home-content">
                    <div className="home-content-header container">
                        <NavLink to="/Home" className="new" activeClassName="active">Новые объявления</NavLink>
                        <NavLink to="/Home/best" className="best" activeClassName="active">Лучщие предложение</NavLink>
                    </div>

                    <div className="home-content-body">
                        <Switch>
                            <Route exact path="/Home" component={HomeNew}/>
                            <Route path="/Home/choice" component={HomeNew}/>
                            <Route path="/Home/reduced" component={HomeNew}/>
                            <Route path="/Home/best" component={HomeBest}/>
                        </Switch>

                        <div className="footer1 container">
                         <h5>Покозать еще</h5>
                          <span><ArrowBold/></span>
                        </div>
                    </div>

                    <div className="home-content-slider">
                    <div className="home-content-slider-inner">
                           <HomeSlider />
                        </div>
                    </div>

                    <div className="home-content-footerSlider container">
                        <HomeFooterSlider/>
                    </div>
            </div>
        </div>
    )
}

export default Home;