import { Link, Switch, Route, Redirect } from 'react-router-dom';
import './main.scss';
import  request   from '../services/http';
import {useRef, useState} from 'react'
import Home from '../pages/Home';

const LoginUp = ({setToken , token}) => {
    const [ hasError, setHasError ] = useState(false)

    const emailInputRef = useRef(null)
    const passwordInputRef = useRef(null)

    const handleSubmit = (e) => {
        e.preventDefault();
        request.post('/login', {
            email: emailInputRef.current.value,
            password: passwordInputRef.current.value,
        })
        .then((response) => {
            window.localStorage.setItem('sessionToken', response.data.token)
            setToken(response.data.token)
            
        })
        .catch((err) => {
            window.location.assign('/Home')
            setHasError(true)
        })
    }
    
    return (
        <div className="loginUp">
            <div className="loginUp-top">
                <h3>
                    Ваша регистрация завершена!
                </h3>
            </div>
            
            <div className="loginUp-row">
                {
                    hasError ? (
                        <p>
                            Hato
                        </p>
                    ) : (
                            <p>
                                Вы успешно зарегистрированы на сайте киви и ваше пароль отправлен в виде смс на ваше телефонный номер
                            </p>
                    )
                }
            </div>

            <form onSubmit={handleSubmit}>
                <div className="loginUp-center">
                    <div className="row">
                        <label htmlFor="">Введите email или номер телефона</label>
                        <div className="row-inner">
                            <input ref={emailInputRef} type="email" placeholder="998 97 772 13 44" />

                        </div>
                    </div>
                    <div className="row1">
                        <label htmlFor="">Пароль</label>
                        <input ref={passwordInputRef} type="password" placeholder="*******" />
                    </div>
                </div>

                <div className="loginUp-btnWrapper">
                    <button type="submit" className="next">Войти</button>
                </div>
            </form>

            <div className="loginUp-bottom">
                <span>
                    или
                </span>


                <a href="">
                    Восстоновить ваше пароль

                </a>
            </div>
        </div>
    )
}

export default LoginUp;