import { Swiper, SwiperSlide } from 'swiper/react';
import ProductBlock from '../conponents/ProductBlock/ProductBlock';
import SidebarPro from '../assets/Products/Products';
import './main.scss';

const HomeFooter3 = () => {
    return (
        <Swiper

            slidesPerView={"auto"}
            grabCursor={true}
            spaceBetween={25}
            centeredSlides={false}

        >

            <div className="homeFooter container">
                {
                    SidebarPro.map((item, index) => (
                        <SwiperSlide>
                            <ProductBlock
                                title={item.title}
                                img={item.img}
                                time={item.time}
                                price={item.price}
                            />
                        </SwiperSlide>
                    ))
                }
            </div>

        </Swiper>
    )
}

export default HomeFooter3;