import './BtnSlider.scss';
import {
    Plane,
    Home,
    Electronics,
    Work,
    Personal,
    HouseAndGarden,
    Construction,
    Hobbies,
    Business,
    ArrowSlider
} from '../../assets/Icons/Icons';

import HomeBtn from '../../conponents/HomeBtn/HomeBtn';
import BtnBlock from '../BtnBlock/BtnBlock';

const Btns = [
    {
        title: 'Автомобили',
        icon: <Plane/>,
        color: '#B6CAFB'
    },
    {
        title: 'Недвижимость',
        icon: <Home/>,
        color: '#B4E2E1'
    },
    {
        title: 'Электроника',
        icon: <Electronics/>,
        color: '#FF9E83'
    },
    {
        title: 'Работа',
        icon: <Work/>,
        color: '#F7E233'
    },
    {
        title: 'Личные вещи',
        icon: <Personal/>,
        color: '#F6DAC2'
    },
    {
        title: ' Дом и Сад',
        icon: <HouseAndGarden/>,
        color: '#B4E2E1'
    },
    {
        title: 'Стройка и ремонт',
        icon: <Construction/>,
        color: '#B6CAFB'
    },
    {
        title: ' Хобби и Спорт',
        icon: <Hobbies/>,
        color: '#F6DAC2'
    },
    {
        title: ' Бизнес и услуги',
        icon: <Business/>,   
        color: '#FF9E83'
    },
]

const BtnSlider = ({classlist, setClasslist}) => {
    return(
        <div className="btnSlider container">
            <button className="prev"><ArrowSlider/></button>
            <div className="btnSlider-content">
                {
                    Btns.map((item, index) => (
                        <HomeBtn
                            title={item.title}
                            icon={item.icon}
                            color={item.color}
                            classlist={classlist}
                            setClasslist={setClasslist}
                        />
                    ))
                }
            </div>
            <button className="next"><ArrowSlider /></button>
        </div>
    )
}

export default BtnSlider;