import Product2 from '../assets/Products/Products2';
import ProductBlock from '../conponents/ProductBlock/ProductBlock';
import './main.scss';

const HomeBest = () => {
    return (
        <div className="homeBest container">
            {
                Product2.map((item, index) => (
                    <ProductBlock
                        title={item.title}
                        img={item.img}
                        time={item.time}
                        price={item.price}
                    />
                ))
            }
        </div>
    )
}

export default HomeBest;