import { useState } from 'react';
import {Link} from 'react-router-dom'
import './BtnBlockAdd.scss';

const BtnBlockAdd = (   ) => {
    const [step2, setStep2] = useState(false)
    const [step3, setStep3] = useState(false)
   

    
    return(
        <div className="wrper">
            <div className="btnBlockAdd container">
                <div className="btnBlockAdd-column">
                    <h3>Рубрика</h3>
                    <div className="btnBlockAdd-column-links">
                        
                        <input type="radio" name="span" id="one1"/>
                        <label className="label" name="span" for="one1" onClick={() => setStep2(true)}>Детский мир</label>
                        <input type="radio" name="span" id="one2"/>
                        <label className="label" name="span" for="one2" onClick={() => setStep2(true)}>Недвижимость</label>
                        <input type="radio" name="span" id="one3"/>
                        <label className="label" name="span" for="one3" onClick={() => setStep2(true)}>Транспорт</label>
                        <input type="radio" name="span" id="one4"/>
                        <label className="label" name="span" for="one4" onClick={() => setStep2(true)}>Работа</label>
                        <input type="radio" name="span" id="one5"/>
                        <label className="label" name="span" for="one5" onClick={() => setStep2(true)}>Животные</label>
                        <input type="radio" name="span" id="one6"/>
                        <label className="label" name="span" for="one6" onClick={() => setStep2(true)}>Дом и сад</label>
                        <input type="radio" name="span" id="one7"/>
                        <label className="label" name="span" for="one7" onClick={() => setStep2(true)}>Электроника</label>
                        <input type="radio" name="span" id="one8"/>
                        <label className="label" name="span" for="one8" onClick={() => setStep2(true)}>Мода и стиль</label>
                        <input type="radio" name="span" id="one9"/>
                        <label className="label" name="span" for="one9" onClick={() => setStep2(true)}>Бизнес и услуги</label>
                        <input type="radio" name="span" id="one10"/>
                        <label className="label" name="span" for="one10" onClick={() => setStep2(true)}>Хобби, отдых и спорт</label>
                    </div>
                </div>
                <div className={`btnBlockAdd-column  ${step2 ? ("active1") : ("")}`}>
                    <h3>Недвижимость</h3>
                    <div className="btnBlockAdd-column-links me">
                        <input type="radio" name="span2" id="two1" />
                        <label className="label" name="span2" for="two1" onClick={() => setStep3(true)}>Квартиры</label>
                        <input type="radio" name="span2" id="two2" />
                        <label className="label" name="span2" for="two2" onClick={() => setStep3(true)}>Дома</label>
                        <input type="radio" name="span2" id="two3" />
                        <label className="label" name="span2" for="two3" onClick={() => setStep3(true)}>Земля</label>
                        <input type="radio" name="span2" id="two4" />
                        <label className="label" name="span2" for="two4" onClick={() => setStep3(true)}>Гаражи / стоянки</label>
                        <input type="radio" name="span2" id="two5" />
                        <label className="label" name="span2" for="two5" onClick={() => setStep3(true)}>Коммерческие помещения</label>
                    </div>
                </div>
                <div className={`btnBlockAdd-column ${step3 ? ("active2") : ("")}`}>
                    <h3>Дома</h3>
                    <div className="btnBlockAdd-column-links me">
                        <Link to="/AddFinish">
                            <input type="radio" name="span3" id="three1" />
                            <label className="label" name="span3" for="three1" >Аренда долгосрочная</label>
                            <input type="radio" name="span3" id="three2" />
                            <label className="label" name="span3" for="three2">Аренда посуточно</label>
                            <input type="radio" name="span3" id="three3" />
                            <label className="label" name="span3" for="three3">Продажа</label>
                            <input type="radio" name="span3" id="three4" />
                            <label className="label" name="span3" for="three4">Обмен</label>
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default BtnBlockAdd;