import './Header.scss';
import {
    Logo
} from '../../assets/Icons/Icons';
import HeaderFilter from '../HeaderFilter/HeaderFilter';
import {Link} from 'react-router-dom'
import Avatar from '../../assets/Images/avatar.png';
import TokenHeader from '../../containers/TokenHeader/TokenHeader';
import { useState } from 'react';

const Header = ({ token, handleExit}) => {
    const [cls, setCls] = useState(false)
    return(
        <div className="header-inner container">
            <div className="header-inner-row1">
                <a href="/Home" className="header-inner-row1__logo">
                    <Logo/>
                    Продай, найди, купи <br/>
                    все что пожелаешь…
                </a>

                <div className="header-inner-row1-links">
                    <Link>Объявления </Link>
                    <Link to="/Filter">Магазины</Link>
                    <Link to="/business">Для бизнеса</Link>
                    <Link>Помощь</Link>
                </div>

                <div className="header-inner-row1-right">
                    <div className="header-inner-row1-right-items">
                        <a href="" className="header-inner-row1-right-items__rus">
                            Рус
                        </a>

                        <span>|</span>

                        <a href="" className="header-inner-row1-right-items__uzb">
                            O’z
                        </a>
                    </div>


                    <div className="header-inner-row1-right-items">
                        {
                            token ? (
                                <div className="user">
                                    <div  className="user1" onClick={() => setCls(!cls)}>
                                        <h3>Темиров</h3>
                                        <img src={Avatar} alt="" />
                                    </div>
                                    <a href="" className={`exit ${cls ? ("active") : ("")}`} onClick={handleExit}>
                                        Exit
                                    </a>
                                    
                                </div>
                            ) : (
                                <div className="header-inner-row1-right-items-yes">
                            <a href="/signup/login" className="header-inner-row1-right-items-yes__login">
                                Вход 
                            </a>

                            <span>|</span>

                            <a href="/signup" className="header-inner-row1-right-items-yes__checkin">
                                Регистрация
                            </a>
                        </div>
                            )
                        }
                    </div>
                </div>
            </div>

            <div className="header-inner-row2">
                <HeaderFilter token={token}/>
            </div>

            <div className="header-inner-row3">
                {
                    token ? (
                        <TokenHeader />
                    ) : (
                        <></>
                    )
                }
            </div>
        </div>
    )
}

export default Header;