import { Swiper, SwiperSlide } from 'swiper/react';
import SidebarPro from '../../assets/Products/SidebarProducts';
import HomeSliderBlock from '../../conponents/HomeSliderBlock/HomeSliderBlock';
import './HomeSlider.scss';

// Import Swiper styles
import 'swiper/swiper.scss';

const HomeSlider = () => {
    return (
        <Swiper
            
            slidesPerView={"auto"}
            grabCursor={true}   
            spaceBetween={30}
            centeredSlides={true}
            
        >

            <div className="homeSlider container">
                {
                    SidebarPro.map((item, index) => (
                        <SwiperSlide>
                            <HomeSliderBlock
                                title={item.title}
                                img={item.img}
                                color1={item.color1}
                                color2={item.color2}
                            />
                        </SwiperSlide>
                    ))
                }
            </div>
      
        </Swiper>
    );
};

export default HomeSlider;