import Products from '../assets/Products/Products';
import ProductBlock from '../conponents/ProductBlock/ProductBlock';
import './main.scss';

const HomeNew = () => {
    return(
        <div className="homeNew container">
            {
                Products.map((item, index) => (
                    <ProductBlock
                        title={item.title}
                        img={item.img}
                        time={item.time}
                        price={item.price}
                    />
                ))
            }
        </div>
    )
}

export default HomeNew;