import { Link } from "react-router-dom";
import './ProductBlock.scss'

const ProductBlock = ({title, img, time, price}) => {
    return(
        <Link className="productBlock" to="/product">
            <div className="productBlock-image">
                <img src={img} alt=""/>
            </div>

            <div className="productBlock-info">
                <h4>{title}</h4>
                <span>{time}</span>
                <h3>{price}</h3>
            </div>
        </Link>
    )
}

export default ProductBlock;