import './main.scss';
import LoginImage from '../assets/Images/login.png';
import { Switch, Route, Link } from 'react-router-dom';
import SignUp1 from '../innerPages/SignUp1';
import SignUp2 from '../innerPages/SignUp2';
import SignUp3 from '../innerPages/SignUp3';
import LoginUp from '../innerPages/LoginUp';

const SignUp = () => {
    return(
        <div className="signup container">
            <div className="signup-left">    
                <Switch>
                    <Route exact path="/signup" component={SignUp1}/>
                    <Route path="/signup/step2" component={SignUp2}/>
                    <Route path="/signup/step3" component={SignUp3}/>
                    <Route path="/signup/login" component={LoginUp}/>
                </Switch>
            </div>

            <div className="signup-right">
                <img src={LoginImage} alt=""/>
            </div>
        </div>
    )
}

export default SignUp;