import './ProductPrice.scss'

const ProductPrice = () => {
    return(
        <div className="productPrice">
            <div className="productPrice-top">
                <span>Артикул: 4418970  </span>
                <span>обновлено: 28.04.17 добавлено: 28.04.17</span>
            </div>

            <div className="productPrice-center">
                <h2>70 000 y.e</h2>
                <div className="productPrice-center-links">
                    <a href="" className="productPrice-center-links__link">Ташкент, Ташкентская область, Юнусабадский район </a>
                    
                </div>

                <div className="productPrice-center-numbers">
                    <span>998 91 166</span>
                    <input type="password" value="1261" id="myInput"/>
                    <button onClick={() => {
                        let x = document.getElementById("myInput");
                        if (x.type === "password") {
                            x.type = "text";
                        } else {
                            x.type = "password";
                        }
                    }}>Покозать польностю</button>
                </div>
                <div className="productPrice-center-numbers">
                    <span>998 91 166</span>
                    <input type="password" value="1261" id="myInput1" />
                    <button onClick={() => {
                        let x = document.getElementById("myInput1");
                        if (x.type === "password") {
                            x.type = "text";
                        } else {
                            x.type = "password";
                        }
                    }}>Покозать польностю</button>
                </div>

                <div className="author">
                    <h5>Автор обьявлении: </h5> <a href="">Зухриддин Темиров</a>
                </div>

            
            </div>

            <div className="productPrice-bottom">
                <button>Написать автору</button>
                <button>Предложить свою цену</button>
            </div>
        </div>
    )
}

export default ProductPrice;

