import './HomeFooterSlider.scss';
import {NavLink, Switch, Route} from 'react-router-dom';
import {HomeFooter1, HomeFooter2, HomeFooter3} from '../../innerPages';
import {ArrowBold} from '../../assets/Icons/Icons';

const HomeFooterSlider = () => {
    return(
        <div className="footerSlider">
            <div className="footerSlider-header">
                <NavLink to="/Home" activeClassName="active" className="one">Просмотренные</NavLink>
                <NavLink to="/Home/choice" activeClassName="active" className="two">Выбор редакции</NavLink>
                <NavLink to="/Home/reduced" activeClassName="active" className="three">Сниженные цены</NavLink>
            </div>

            <div className="footerSlider-body">
                <Switch>
                    <Route exact path="/Home" component={HomeFooter1}/>
                    <Route path="/Home/choice" component={HomeFooter2}/>
                    <Route path="/Home/reduced" component={HomeFooter3}/>
                </Switch>
            </div>

            <div className="footerSlider-footer">
                <h4>Покозать еще</h4> <span><ArrowBold/></span>
            </div>
        </div>
    )
}

export default HomeFooterSlider;