import './main.scss';
import ProductImage from '../conponents/ProductImage/ProductImage';
import ProductPrice from '../conponents/ProductPrice/ProductPrice';
import ProductInfo from '../containers/ProductInfo/ProductInfo';    
import ChekinFooterSlider from '../containers/ChekinFooterSlider/ChekinFooterSlider';

const Checkin = () => {
    return(
        <div className="product container">
            <div className="product-header">
                <span>Объявления / Ташкент Недвижимость / Ташкент Квартиры / Ташкент Продажа / Новостройки Ташкент</span>

                <div className="product-header-title">
                    <h1>Срочно Голден Хаус Етти Чинор Новостройка 4х ком на 1 этаже юнусабад 5</h1>
                </div>
            </div>

            <div className="product-body">
                <div className="product-body-left">
                    <ProductImage/>
                </div>

                <div className="product-body-right">
                    <ProductPrice/>
                    <ProductInfo/>

                    <div className="product-body-right-text">
                        <p>
                            Срочно продаётся 3х ком на 1 этоже 7 этажном кирпичном доме. Новостройка Голден Хаус жылой комплекс Етти Чинор Очень качественный и дорогой ремонт. Комнаты полнастю разделные болшые. 2 сан узла. Для лишних вещей есть кладовка.Ремонт шыкарный обсалютно новый. <br/> <br/>

                            Ремонт делолся из очень дорогова материяла. Дорогая класическая кухонная мебель. Джаккузи,Тёплые полы в каждой комнате. Дорогие люстры вовсех комнатах решодки на окнах. Теретория охранается. Всё предусмотренно для детей деские площядки качели итд. Торг наместе. Торопитесь продаю нужны денги. Первый золотой этаж и паследний в этом коплексе. Торопитесь.
                        </p>

                        <div className="row">
                            <span>Просмотры:10958</span>

                            <a href="">Пожаловатся</a>
                        </div>
                    </div>
                </div>
            </div>

            <div className="product-footer">
                <ChekinFooterSlider/>
            </div>
        </div>
    )
}

export default Checkin;