import {Link} from 'react-router-dom';
import './main.scss';

const SignUp1 = () => {
    return(
        <div className="signup1">
            <div className="signup1-top">
                <h3>
                    Бесплатная регистрация
                </h3>
            </div>

            <div className="signup1-center">
                <input type="text" placeholder="Темиров Зухриддин" />
                <Link to="/signup/step2">Далее</Link>
            </div>

            <div className="signup1-bottom">
                <span>
                    или
                </span>
                

                <a href="">
                    Войдите в систему, если у вас уже есть <br/>
                     аккаунт kivi.uz

                </a>
            </div>
        </div>
    )   
}

export default SignUp1;