import './ChekinFooterSlider.scss';
import {NavLink, Switch, Route} from 'react-router-dom';
import {HomeFooter1, HomeFooter2} from '../../innerPages';

const HomeFooterSlider = () => {
    return(
        <div className="footerSlider">
            <div className="footerSlider-header">
                <NavLink to="/product" activeClassName="active" className="one">Похожие объявления</NavLink>
                <NavLink to="/product/elon" activeClassName="active" className="two">Обьявление автора</NavLink>
            </div>

            <div className="footerSlider-body">
                <Switch>
                    <Route exact path="/product" component={HomeFooter1}/>
                    <Route path="/product/elon" component={HomeFooter2}/>
                </Switch>
            </div>
        </div>
    )
}

export default HomeFooterSlider;